*{
    margin: 0px;
    padding: 0px;
    background-color: #f28ab2;
}
.cardContainer{
    display: flex;
    flex-direction: column;
    gap: 20px;
    border-radius: 5px;
    width: 380px;
    padding: 30px;
    box-shadow: 1px 3px 9px 2px #5e5d5d;
}
h2, article, p, h4, .card,.cardContainer, .nameAndAgeDiv{
    background-color: white;
}
.card{
    display: flex;
}
article{
    display: flex;
    gap: 10px;
}
.card img{
    width: 75px;
    height: 75px;
    border-radius: 50%;
}
.nameAndAgeDiv{
    display: flex;
    flex-direction: column;
}
button{
    border: none;
    border-radius: 5px;
    font-size: 20px;
    padding: 10px;
    color: white;
}